var formatBytes, getUploadPreview, readAsArrayBuffer, readAsDataURL;

readAsDataURL = function(file, callback) {
  var reader;
  reader = new FileReader();
  reader.onload = function(ev) {
    return callback(ev.target.result, file);
  };
  return reader.readAsDataURL(file);
};

getUploadPreview = function(file, callback) {
  if (file.file.size > 10 * 1000000) {
    return callback(file, null);
  } else if (file.file.type == null) {
    return callback(file, null);
  } else {
    if (file.file.type.indexOf('audio') > -1 || file.file.type.indexOf('video') > -1 || file.file.type.indexOf('image') > -1) {
      file.type = file.file.type.split('/')[0];
      return readAsDataURL(file.file, function(content) {
        return callback(file, content);
      });
    } else {
      return callback(file, null);
    }
  }
};

formatBytes = function(bytes, decimals) {
  var dm, i, k, sizes;
  if (bytes === 0) {
    return '0 Bytes';
  }
  k = 1000;
  dm = decimals + 1 || 3;
  sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB'];
  i = Math.floor(Math.log(bytes) / Math.log(k));
  return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
};

readAsArrayBuffer = function(file, callback) {
  var reader;
  reader = new FileReader();
  reader.onload = function(ev) {
    return callback(ev.target.result, file);
  };
  return reader.readAsArrayBuffer(file);
};

this.fileUpload = function(files) {
  var consume, roomId;
  roomId = Session.get('openedRoom');
  files = [].concat(files);
  consume = function() {
    var file;
    file = files.pop();
    if (file == null) {
      swal.close();
      return;
    }
    if (!RocketChat.fileUploadIsValidContentType(file.file.type)) {
      swal({
        title: t('FileUpload_MediaType_NotAccepted'),
        text: file.file.type || ("*." + (s.strRightBack(file.file.name, '.'))),
        type: 'error',
        timer: 3000
      });
      return;
    }
    if (file.file.size === 0) {
      swal({
        title: t('FileUpload_File_Empty'),
        type: 'error',
        timer: 1000
      });
      return;
    }
    return getUploadPreview(file, function(file, preview) {
      var fileSize, text;
      text = '';
      if (file.type === 'audio') {
        text = "<div class='upload-preview'>\n	<audio  style=\"width: 100%;\" controls=\"controls\">\n		<source src=\"" + preview + "\" type=\"audio/wav\">\n		Your browser does not support the audio element.\n	</audio>\n</div>\n<div class='upload-preview-title'>\n	<input id='file-name' style='display: inherit;' value='" + (Handlebars._escape(file.name)) + "' placeholder='" + (t("Upload_file_name")) + "'>\n	<input id='file-description' style='display: inherit;' value='' placeholder='" + (t("Upload_file_description")) + "'>\n</div>";
      } else if (file.type === 'video') {
        text = "<div class='upload-preview'>\n	<video  style=\"width: 100%;\" controls=\"controls\">\n		<source src=\"" + preview + "\" type=\"video/webm\">\n		Your browser does not support the video element.\n	</video>\n</div>\n<div class='upload-preview-title'>\n	<input id='file-name' style='display: inherit;' value='" + (Handlebars._escape(file.name)) + "' placeholder='" + (t("Upload_file_name")) + "'>\n	<input id='file-description' style='display: inherit;' value='' placeholder='" + (t("Upload_file_description")) + "'>\n</div>";
      } else if (file.type === 'image') {
        text = "<div class='upload-preview'>\n	<div class='upload-preview-file' style='background-image: url(" + preview + ")'></div>\n</div>\n<div class='upload-preview-title'>\n	<input id='file-name' style='display: inherit;' value='" + (Handlebars._escape(file.name)) + "' placeholder='" + (t("Upload_file_name")) + "'>\n	<input id='file-description' style='display: inherit;' value='' placeholder='" + (t("Upload_file_description")) + "'>\n</div>";
      } else {
        fileSize = formatBytes(file.file.size);
        text = "<div class='upload-preview'>\n	<div>" + (Handlebars._escape(file.name)) + " - " + fileSize + "</div>\n</div>\n<div class='upload-preview-title'>\n	<input id='file-name' style='display: inherit;' value='" + (Handlebars._escape(file.name)) + "' placeholder='" + (t("Upload_file_name")) + "'>\n	<input id='file-description' style='display: inherit;' value='' placeholder='" + (t("Upload_file_description")) + "'>\n</div>";
      }
      return swal({
        title: t('Upload_file_question'),
        text: text,
        showCancelButton: true,
        closeOnConfirm: false,
        closeOnCancel: false,
        confirmButtonText: t('Send'),
        cancelButtonText: t('Cancel'),
        html: true
      }, function(isConfirm) {
        var record, upload, uploading;
        consume();
        if (isConfirm !== true) {
          return;
        }
        record = {
          name: document.getElementById('file-name').value || file.name || file.file.name,
          size: file.file.size,
          type: file.file.type,
          rid: roomId,
          description: document.getElementById('file-description').value
        };
        upload = fileUploadHandler(record, file.file);
        uploading = Session.get('uploading') || [];
        uploading.push({
          id: upload.id,
          name: upload.getFileName(),
          percentage: 0
        });
        Session.set('uploading', uploading);
        upload.onProgress = function(progress) {
          var item;
          uploading = Session.get('uploading');
          item = _.findWhere(uploading, {
            id: upload.id
          });
          if (item != null) {
            item.percentage = Math.round(progress * 100) || 0;
            return Session.set('uploading', uploading);
          }
        };
        upload.start();
        return Tracker.autorun(function(c) {
          var cancel, item;
          cancel = Session.get("uploading-cancel-" + upload.id);
          if (cancel) {
            upload.stop();
            c.stop();
            uploading = Session.get('uploading');
            if (uploading != null) {
              item = _.findWhere(uploading, {
                id: upload.id
              });
              if (item != null) {
                item.percentage = 0;
              }
              Session.set('uploading', uploading);
            }
            return Meteor.setTimeout(function() {
              uploading = Session.get('uploading');
              if (uploading != null) {
                item = _.findWhere(uploading, {
                  id: upload.id
                });
                return Session.set('uploading', _.without(uploading, item));
              }
            }, 1000);
          }
        });
      });
    });
  };
  return consume();
};
