
/* DEFINITIONS
- If window loses focus user needs to scroll or click/touch some place
- On hit ESC enable read, force read of current room and remove unread mark
- When user change room disable read until user interaction
- Only read if mark of *first-unread* is visible for user or if flag *force* was passed
- Always read the opened room
- The default method *read* has a delay of 2000ms to prevent multiple reads and to user be able to see the mark
 */
this.readMessage = new ((function() {
  _Class.prototype.debug = false;

  _Class.prototype.callbacks = [];

  function _Class() {
    this.canReadMessage = false;
  }

  _Class.prototype.readNow = function(force) {
    var position, rid, room, self, subscription, unreadMark, visible;
    if (force == null) {
      force = false;
    }
    if (this.debug) {
      console.log('--------------');
    }
    if (this.debug) {
      console.log('readMessage -> readNow init process force:', force);
    }
    self = this;
    self.refreshUnreadMark();
    if (force !== true && this.canReadMessage === false) {
      if (this.debug) {
        console.log('readMessage -> readNow canceled by canReadMessage: false');
      }
      return;
    }
    rid = Session.get('openedRoom');
    if (rid == null) {
      if (this.debug) {
        console.log('readMessage -> readNow canceled, no rid informed');
      }
      return;
    }
    if (force === true) {
      if (this.debug) {
        console.log('readMessage -> readNow via force rid:', rid);
      }
      return Meteor.call('readMessages', rid, function() {
        RoomHistoryManager.getRoom(rid).unreadNotLoaded.set(0);
        self.refreshUnreadMark();
        return self.fireRead(rid);
      });
    }
    subscription = ChatSubscription.findOne({
      rid: rid
    });
    if (subscription == null) {
      if (this.debug) {
        console.log('readMessage -> readNow canceled, no subscription found for rid:', rid);
      }
      return;
    }
    if (subscription.alert === false && subscription.unread === 0) {
      if (this.debug) {
        console.log('readMessage -> readNow canceled, alert', subscription.alert, 'and unread', subscription.unread);
      }
      return;
    }
    room = RoomManager.getOpenedRoomByRid(rid);
    if (room == null) {
      if (this.debug) {
        console.log('readMessage -> readNow canceled, no room found for typeName:', subscription.t + subscription.name);
      }
      return;
    }
    unreadMark = $('.message.first-unread');
    if (unreadMark.length > 0) {
      position = unreadMark.position();
      visible = (position != null ? position.top : void 0) >= 0;
      if (!visible && (room.unreadSince.get() != null)) {
        if (this.debug) {
          console.log('readMessage -> readNow canceled, unread mark visible:', visible, 'unread since exists', room.unreadSince.get() != null);
        }
        return;
      }
    }
    if (this.debug) {
      console.log('readMessage -> readNow rid:', rid);
    }
    return Meteor.call('readMessages', rid, function() {
      RoomHistoryManager.getRoom(rid).unreadNotLoaded.set(0);
      self.refreshUnreadMark();
      return self.fireRead(rid);
    });
  };

  _Class.prototype.read = _.debounce(function(force) {
    return this.readNow(force);
  }, 1000);

  _Class.prototype.disable = function() {
    return this.canReadMessage = false;
  };

  _Class.prototype.enable = function() {
    return this.canReadMessage = document.hasFocus();
  };

  _Class.prototype.isEnable = function() {
    return this.canReadMessage === true;
  };

  _Class.prototype.onRead = function(cb) {
    return this.callbacks.push(cb);
  };

  _Class.prototype.fireRead = function(rid) {
    var cb, i, len, ref, results;
    ref = this.callbacks;
    results = [];
    for (i = 0, len = ref.length; i < len; i++) {
      cb = ref[i];
      results.push(cb(rid));
    }
    return results;
  };

  _Class.prototype.refreshUnreadMark = function(rid, force) {
    var $roomDom, firstUnreadRecord, lastReadRecord, room, self, subscription;
    self = this;
    if (rid == null) {
      rid = Session.get('openedRoom');
    }
    if (rid == null) {
      return;
    }
    subscription = ChatSubscription.findOne({
      rid: rid
    }, {
      reactive: false
    });
    if (subscription == null) {
      return;
    }
    room = RoomManager.openedRooms[subscription.t + subscription.name];
    if (room == null) {
      return;
    }
    $roomDom = $(room.dom);
    $roomDom.find('.message.first-unread').addClass('first-unread-opaque');
    if (!subscription.alert && subscription.unread === 0) {
      room.unreadSince.set(void 0);
      return;
    }
    if ((force == null) && subscription.rid === Session.get('openedRoom') && document.hasFocus()) {
      return;
    }
    $roomDom.find('.message.first-unread').removeClass('first-unread').removeClass('first-unread-opaque');
    lastReadRecord = ChatMessage.findOne({
      rid: subscription.rid,
      ts: {
        $lt: subscription.ls
      }
    }, {
      sort: {
        ts: -1
      }
    });
    if ((lastReadRecord == null) && RoomHistoryManager.getRoom(room.rid).unreadNotLoaded.get() === 0) {
      lastReadRecord = {
        ts: new Date(0)
      };
    }
    if ((lastReadRecord != null) || RoomHistoryManager.getRoom(room.rid).unreadNotLoaded.get() > 0) {
      room.unreadSince.set(subscription.ls);
    } else {
      room.unreadSince.set(void 0);
    }
    if (lastReadRecord != null) {
      firstUnreadRecord = ChatMessage.findOne({
        rid: subscription.rid,
        ts: {
          $gt: lastReadRecord.ts
        },
        'u._id': {
          $ne: Meteor.userId()
        }
      }, {
        sort: {
          ts: 1
        }
      });
      if (firstUnreadRecord != null) {
        room.unreadFirstId = firstUnreadRecord._id;
        return $roomDom.find('.message#' + firstUnreadRecord._id).addClass('first-unread');
      }
    }
  };

  return _Class;

})());

Meteor.startup(function() {
  $(window).on('blur', function() {
    return readMessage.disable();
  });
  $(window).on('focus', function() {
    readMessage.enable();
    return readMessage.read();
  });
  $(window).on('click', function(e) {
    readMessage.enable();
    return readMessage.read();
  });
  $(window).on('touchend', function(e) {
    readMessage.enable();
    return readMessage.read();
  });
  return $(window).on('keyup', function(e) {
    var key;
    key = e.which;
    if (key === 27) {
      readMessage.enable();
      readMessage.readNow(true);
      return $('.message.first-unread').removeClass('first-unread');
    }
  });
});
