if (Meteor.isCordova) {
  Push.addListener('startup', function(notification) {
    var host, path, ref;
    if (((ref = notification.payload) != null ? ref.rid : void 0) != null) {
      if (notification.payload.host === Meteor.absoluteUrl()) {
        switch (notification.payload.type) {
          case 'c':
            return FlowRouter.go('channel', {
              name: notification.payload.name
            }, FlowRouter.current().queryParams);
          case 'p':
            return FlowRouter.go('group', {
              name: notification.payload.name
            }, FlowRouter.current().queryParams);
          case 'd':
            return FlowRouter.go('direct', {
              username: notification.payload.sender.username
            }, FlowRouter.current().queryParams);
        }
      } else {
        path = '';
        switch (notification.payload.type) {
          case 'c':
            path = 'channel/' + notification.payload.name;
            break;
          case 'p':
            path = 'group/' + notification.payload.name;
            break;
          case 'd':
            path = 'direct/' + notification.payload.sender.username;
        }
        host = notification.payload.host.replace(/\/$/, '');
        if (Servers.serverExists(host) !== true) {
          return;
        }
        return Servers.startServer(host, path, function(err, url) {
          if (err != null) {
            return console.log(err);
          }
        });
      }
    }
  });
  Meteor.startup(function() {
    return Tracker.autorun(function() {
      if (RocketChat.settings.get('Push_enable') === true) {
        return Push.Configure({
          android: {
            senderID: window.ANDROID_SENDER_ID,
            sound: true,
            vibrate: true
          },
          ios: {
            badge: true,
            clearBadge: true,
            sound: true,
            alert: true
          }
        });
      }
    });
  });
}
